// ********************
// GRID
// ********************

// GRID COLUMNS
.grid-cols-2 {
    @include gridRows(2);

    @include breakpointsUp(medium) {
        grid-template-rows: none;
        @include gridColumns(2);
    }

}

.grid-cols-3 {
    @include gridRows(3);

    @include breakpointsUp(medium) {
        grid-template-rows: none;
        @include gridColumns(3);
    }
}

.grid-cols-6 {
    display: grid;
    grid-template-rows: repeat(3, minmax(0, 1fr));
    grid-template-columns: repeat(2, minmax(0, 1fr));

    @include breakpointsUp(large) {
        grid-template-rows: none;
        grid-template-columns: repeat(6, minmax(0, 1fr));
    }

}

.place-items-center {
    place-items: center;
}

// GRID ROWS
.grid-rows-2 {
    @include gridRows(2);
}

.grid-rows-3 {
    @include gridRows(3);
}

// GRID GAP
.gap-16 {
    gap: 1rem;

    @include breakpointsDown(medium) {
        gap: 0.5rem;
    }

}

.gap-32 {
    gap: 2rem;

    @include breakpointsDown(medium) {
        gap: 1rem;
    }

}

.gap-64 {
    gap: 4rem;

    @include breakpointsDown(medium) {
        gap: 2rem;
    }

}