a[data-hs-anchor="true"] {   
    display: block; 
    position: relative; 
    visibility: hidden; 

    @include breakpointsDown(medium) {
        top: 8rem;
    }

    @include breakpointsUp(medium) {
        top: 11rem;
    }
}


// ********************
// HERO SECTION
// ********************

#hero__video {
    --video-width: 100vw;

    width: var(--video-width);
    height: calc(0.5625 * var(--video-width));
}

#hero__video-wrapper {
    position: relative;

    > div:first-of-type {
        position: absolute;
        z-index: 200;
        top: -1px;
        right: 0;
        left: 0;
    }
}

#ribbon {
    margin-top: 1px;
    padding: 0 1.5rem;
    width: 35.625rem; height: 2.5rem;
    background-color: #CE0000;
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
    cursor: pointer;

    p {
        color: $white;
    }

}

#collage {
    width: 100%; height: auto;
    filter: drop-shadow(0 25px 50px rgba($gray, 0.25));
}

// ********************
// PROOF SECTION
// ********************

.proof__logo {
    margin: 2.5rem 1.25rem;
    width: auto; height: 3rem;

    @include breakpointsDown(medium) {
        height: 1.5rem;
    }

    &.large {
        height: 4.5rem;

        @include breakpointsDown(medium) {
            height: 2.25rem;
        }

    }

}

// ********************
// TRANSFORM SECTION
// ********************

#transform__intro {

    margin-bottom: 1.25rem;

    @include breakpointsUp(medium) {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-bottom: 5rem;

        > div {
            max-width: 50%;
        }

    }

}

#transform__intro__left {
    padding-bottom: 1.25rem;

    @include breakpointsUp(medium) {
        padding-right: 1.5rem;
    }

}

#transform__intro__right {

    @include breakpointsUp(medium) {
        padding-left: 1.5rem;
    }

}

// ********************
// SCHEDULE SECTION
// ********************

.schedule__grid-row {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    grid-template-rows: repeat(2, minmax(0, min-content));
    place-items: center;
    gap: 3.75rem;

    @include breakpointsUp(large) {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-template-rows: repeat(1, minmax(0, 1fr));
        gap: 7.5rem;
    }

    &:not(:last-of-type) {
        margin-bottom: 2.5rem;

        @include breakpointsUp(medium) {
            margin-bottom: 5rem;
        }

    }

}

.schedule__grid-row__copy {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.schedule__grid-row__text {

    &:not(:last-of-type) {
        margin-bottom: 1.25rem;
    }

}

.schedule__grid-row__eyebrow {
    margin-bottom: 1.25rem;
    font-size: 1.25rem;
    line-height: 1;
    font-weight: 700;
    text-transform: uppercase;
    color: $gold;
}

.schedule__grid__list {

    li {
        position: relative;
        padding-left: 1rem;
        font-weight: 700;

        &::before {
            content: '';
            position: absolute;
            top: 0.5rem; left: 0;
            width: 0.5rem; height: 0.5rem;
            background-color: #333333;
            border-radius: 100vw;
        }

    }

    li:not(:last-of-type) {
        margin-bottom: 0.625rem;
    }

}

#day1, #day2, #day3 {

    >:first-child {
        order: 2;
    }

    >:last-child {
        order: 1;
    }

    @include breakpointsUp(medium) {

        >:first-child {
            order: 1;
        }
    
        >:last-child {
            order: 2;
        }

    }
}

#day1-evening, #day2-evening, #day3-evening {

    >:first-child {
        order: 2;
    }

    >:last-child {
        order: 1;
    }

} 

.schedule__grid-row__img {
    box-shadow: 0px 25px 50px -12px rgba(0, 0, 0, 0.25);
}

// ********************
// LEVEL UP SECTION
// ********************

#level__overlay {
    position: absolute;
    z-index: 50;
    top: 0; right: 0; bottom: 0; left: 0;
    width: 100%; height: 100%;
    backdrop-filter: brightness(80%) saturate(80%);

    @include breakpointsDown(small) {
        background-color: rgba($gray, 0.99);
    }

    @include breakpointsUp(small) {
        background-color: rgba($gray, 0);
        background-image: linear-gradient(to right, rgba($gray, 0), rgba($gray, 1.0) 55%);
    }
}

#level {

    &::before {
        content: '';
        position: absolute;
        top: 0; left: 0;
        width: 100%; height: 100%;
        background-image: url('static/img/matt_level.webp'), url('static/img/matt_level.jpg');
        background-repeat: no-repeat;
        background-size: auto 125%;

        // Below 639px;
        @media(max-width: map-get($breakpoints-down, 'small')) {
            background-position: -200vw -5vw
        }
        
        // From 640px to 767px;
        @media(min-width: map-get($breakpoints-up, 'small')) and (max-width: map-get($breakpoints-down, 'medium')) {
            background-position: -180vw -5vw
        }

        // From 768px to 1023px;
        @media(min-width: map-get($breakpoints-up, 'medium')) and (max-width: map-get($breakpoints-down, 'large')) {
            background-position: -95vw -5vw
        }

        // From 1024px to 1279px;
        @media(min-width: map-get($breakpoints-up, 'large')) and (max-width: map-get($breakpoints-down, 'xl')) {
            background-position: -60vw -5vw
        }

        // From 1280px to 1535px;
        @media(min-width: map-get($breakpoints-up, 'xl')) and (max-width: map-get($breakpoints-down, '2xl')) {
            background-position: -35vw -5vw;
        }

        // Above 1535px
        @media(min-width: map-get($breakpoints-up, '2xl')) {
            background-position: -10vw -5vw;
        }

    }

}

#polaroid {
    padding-top: 0.875rem;
    padding-right: 0.875rem;
    padding-bottom: 3.5rem;
    padding-left: 0.875rem;
    background-color: $white;
    box-shadow: 0px 10px 15px -3px rgba($color: #000000, $alpha: 0.1), 0px 2px 4px -1px rgba($color: #000000, $alpha: 0.06);
    transform: rotateZ(-5deg);

    img {
        width: 12.5rem;
        height: auto;
    }

}

// ********************
// WHO SECTION
// ********************

.tab {
    position: relative;
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.25rem;
    transition: all ease-in-out 0.3s;
    cursor: pointer;

    &:hover {
        background-color: rgba($gray, 0.05);
    }

    &.is-active {
        background-color: $white;
        box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1), 0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);
    }

    h3 {
        font-weight: 700;
    }

}

.is-active {

    @include breakpointsUp(medium) {
        &:after {
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            width: 0;
            height: 0;
            border: 1rem solid transparent;
            border-left-color: $white;
            border-right: 0;
            margin-top: -1rem;
            margin-right: -1rem;
        }
    }

}

.tab__content {
    padding: 2.5rem;
    border: 2px dashed $gold;
    border-radius: 0.5rem;
    background-color: $white;
    box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1), 0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);
}

// ********************
// READY SECTION
// ********************

#ready {

    &::before {
        content: '';
        position: absolute;
        top: 0; left: 0;
        width: 100%; height: 100%;
        background-image: url('static/img/matt_ready.webp'), url('static/img/matt_ready.jpg');
        background-repeat: no-repeat;
        background-size: auto 200%;
        background-position: center;
    }

}

#ready__overlay {
    position: absolute;
    z-index: 50;
    top: 0; right: 0; bottom: 0; left: 0;
    width: 100%; height: 100%;
    background-image: linear-gradient(to right, rgba($gray, 1.0), rgba($gray, 1.0));
    backdrop-filter: brightness(60%) saturate(60%);

    @include breakpointsUp(small) {
        background-image: linear-gradient(to right, rgba($gray, 1.0) 25%, rgba($gray, 0.5) 50%, rgba($gray, 1.0) 75%);
    }

}

.cta {
    width: 100%;
    padding: 5rem 2.5rem;
    background-color: rgba($white, 0.1);
    border: 2px dashed $gold;
    border-radius: 0.5rem;

    h3 {
        color: $white;
    }

}

// ********************
// BONUS SECTION
// ********************

.bonus__img-wrapper {
    position: relative;
    width: 100%; height: auto;
    border-radius: 0.25rem;
    overflow: hidden;

    &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0; right: 0; bottom: 0; left: 0;
        width: 100%; height: 100%;
        background: rgba($gray, 0.2);
        backdrop-filter: brightness(90%) saturate(90%);
    }

}

.bonus__img {
    width: 100%; height: 100%;
}

