html {
  scroll-behavior: smooth;
}

body {
  font-family: "Avenir", sans-serif;
  overflow-x: hidden;
}

main {
  margin-top: 8.5rem;
}
@media (min-width: 48rem) {
  main {
    margin-top: 11.5rem;
  }
}

.section {
  position: relative;
}
.section.is-gray {
  background-color: rgba(52, 51, 62, 0.05);
}
.section.is-dark-gray {
  background-color: #34333E;
}
.section.is-dark-gray h2, .section.is-dark-gray h3, .section.is-dark-gray h4 {
  color: #FFFFFF;
}
.section.is-dark-gray p {
  color: rgba(255, 255, 255, 0.8);
}
.section.is-dark-gray p strong {
  color: white;
}

.container {
  margin: 0 auto;
  width: 100%;
}
.container > * {
  position: relative;
  z-index: 100;
}
@media (max-width: 39.9375rem) {
  .container {
    padding: 0 1rem;
  }
}
@media (min-width: 40rem) and (max-width: 47.9375rem) {
  .container {
    max-width: 40rem;
    padding: 0 2rem;
  }
}
@media (min-width: 48rem) and (max-width: 63.9375rem) {
  .container {
    max-width: 48rem;
    padding: 0 3rem;
  }
}
@media (min-width: 64rem) and (max-width: 79.9375rem) {
  .container {
    max-width: 64rem;
    padding: 0 4rem;
  }
}
@media (min-width: 80rem) and (max-width: 95.9375rem) {
  .container {
    max-width: 80rem;
    padding: 0 5rem;
  }
}
@media (min-width: 96rem) {
  .container {
    max-width: 96rem;
    padding: 0 6rem;
  }
}

.helmet {
  margin: 0 auto;
  max-width: 65ch;
}

