}
.flex-row.justify-around {
  justify-content: space-around;
}
.flex-row.justify-evenly {
  justify-content: space-evenly;
}
.flex-row.items-start {
  align-items: flex-start;
}
.flex-row.items-end {
  align-items: flex-end;
}
.flex-row.items-center {
  align-items: center;
}
.flex-row.items-baseline {
  align-items: baseline;
}
.flex-row.items-stretch {
  align-items: stretch;
}

.flex-col {
  display: flex;
  flex-direction: column;
}
.flex-col.justify-start {
  align-items: flex-start;
}
.flex-col.justify-end {
  align-items: flex-end;
}
.flex-col.justify-center {
  align-items: center;
}
.flex-col.justify-between {
  align-items: space-between;
}
.flex-col.justify-around {
  align-items: space-around;
}
.flex-col.justify-evenly {
  align-items: space-evenly;
}
.flex-col.items-start {
  justify-content: flex-start;
}
.flex-col.items-end {
  justify-content: flex-end;
}
.flex-col.items-center {
  justify-content: center;
}
.flex-col.items-baseline {
  justify-content: baseline;
}
.flex-col.items-stretch {
  justify-content: stretch;
}

.wrap {
  flex-wrap: wrap;
}

.no-wrap {
  flex-wrap: nowrap;
}

@font-face {
  font-family: "Avenir";
  src: url("static/fonts/AvenirLTStd-Book.otf") format("opentype");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Avenir";
  src: url("static/fonts/AvenirLTStd-Roman.otf") format("opentype");
  font-weight: 500;
  font-display: swap;
}
