  font-family: "Avenir";
  src: url("static/fonts/AvenirLTStd-Black.otf") format("opentype");
  font-weight: 900;
  font-display: swap;
}
.grid-cols-2 {
  display: grid;
  grid-template-rows: repeat(2, minmax(0, 1fr));
}
@media (min-width: 48rem) {
  .grid-cols-2 {
    grid-template-rows: none;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

.grid-cols-3 {
  display: grid;
  grid-template-rows: repeat(3, minmax(0, 1fr));
}
@media (min-width: 48rem) {
  .grid-cols-3 {
    grid-template-rows: none;
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

.grid-cols-6 {
  display: grid;
  grid-template-rows: repeat(3, minmax(0, 1fr));
  grid-template-columns: repeat(2, minmax(0, 1fr));
}
@media (min-width: 64rem) {
  .grid-cols-6 {
    grid-template-rows: none;
    grid-template-columns: repeat(6, minmax(0, 1fr));
  }
}

.place-items-center {
  place-items: center;
}

.grid-rows-2 {
  display: grid;
  grid-template-rows: repeat(2, minmax(0, 1fr));
}

.grid-rows-3 {
  display: grid;
  grid-template-rows: repeat(3, minmax(0, 1fr));
}

.gap-16 {
  gap: 1rem;
}
@media (max-width: 47.9375rem) {
  .gap-16 {
    gap: 0.5rem;
  }
}

.gap-32 {
  gap: 2rem;
}
@media (max-width: 47.9375rem) {
  .gap-32 {
    gap: 1rem;
  }
}

.gap-64 {
  gap: 4rem;
}
@media (max-width: 47.9375rem) {
  .gap-64 {
    gap: 2rem;
  }
}

