#countdown__heading {
    margin-bottom: 1rem;
    font-size: 1.25rem;
    line-height: 1;
    font-weight: 700;
    color: $white;

}

.countdown__wrapper, .countdown__wrapper--small {
    height: 100%;

    > p {
        margin-bottom: 0.25rem;
        color: $white;
    }

    > h3 {
        font-weight: 500;
        text-transform: uppercase;
        color: rgba($white, 0.5);
    }

}

.countdown__wrapper {
    width: 7.5rem;
}

.countdown__wrapper--small {
    width: 5rem;
}