// ********************
// CAROUSEL
// ********************

.embla {
    overflow: hidden;

    @include breakpointsUp(medium) {

        &::before {
            content: '';
            position: absolute;
            z-index: 200;
            top: 0; left: 0;
            width: 100%; height: 100%;
            background-image: linear-gradient(to right, rgba($white, 1.0) 1%, rgba($white, 0) 50%, rgba($white, 1.0) 99%);
            pointer-events: none;

        }
    }

}

.embla__viewport {
    position: relative;
    pointer-events: auto;
}

.embla__container {
    display: flex;
}

.embla__slide {
    margin: 0 1.25rem;
    padding: 1.25rem 0;
    position: relative;

    @include breakpointsDown(medium) {
        flex: 0 0 100%;
    }

    @include breakpointsUp(medium) {
        flex: 0 0 50%;
    }

    > p {
        padding: 2.5rem;
        opacity: 0.8;
        font-size: 1.125rem;
    }

}

.embla__btn-wrapper {
    position: absolute;
    z-index: 300;
    top: 0;
    height: 100%;

    &.left {
        left: 1.25rem;
    }

    &.right {
        right: 1.25rem;
    }

}

.embla__prev, .embla__next {
    width: 3rem;
    height: 3rem;
    margin-top: -400%;
    border-radius: 50%;
    box-shadow: 0px 1px 3px 0px rgba($color: #000000, $alpha: 0.1), 0px 1px 2px 0px rgba($color: #000000, $alpha: 0.06);
    background-color: $white;
}

.thumbnail {
    width: 100%;
    height: auto;
    max-width: 40rem;
    border: 0.5rem solid $white;
    box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1), 0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);
}