form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;

    input {
        padding: 1.25rem;
        width: 100%;
    }

    input[type=text], input[type=email], input[type=tel] {
        background-color: rgba($white, 0.25);
        color: $white;

        &:focus {
            outline: 1px solid rgba($white, 0.8);
        }

        &::placeholder {
            font-size: 1.125rem;
            font-weight: 500;
            color: rgba($white, 0.5);
        }

    }

    button {
        width: 100%;
        box-shadow: 0px 10px 15px -3px rgba($color: #000000, $alpha: 0.1), 0px 2px 4px -1px rgba($color: #000000, $alpha: 0.06);
        padding: 1.5rem 3rem;
        font-size: 1.25rem;
    }

}
